import "./products.css";
import useFetch from "../hooks/useFetch";
import "chart.js/auto";
import { Chart } from "react-chartjs-2";
import axios from "axios";
import { useState } from "react";

const Body = () => {
  const [scanCode, setScanCode] = useState("");

  const deleteEntry = async () => {
    const url = `https://barcodefinder.herokuapp.com/api/prods/add/${scanCode}`;
    const res = await axios.put(
      url, //https://barcodefinder.herokuapp.com/
      {}
    );
    console.log(res);
    window.location.reload();
  };
  const currentDay = new Date();
  var { data, loading, error } = useFetch(
    "https://barcodefinder.herokuapp.com/api/prods"
  );
  const dataFiltered = data.filter(
    (item) =>
      item.scanned === true &&
      (new Date(item.expiry).getTime() - currentDay.getTime()) /
        (1000 * 3600 * 24) <=
        3
  );
  const dataFiltered2 = data.filter(
    (item) =>
      item.scanned === true &&
      (new Date(item.expiry).getTime() - currentDay.getTime()) /
        (1000 * 3600 * 24) >
        3
  );
  const vegetableData = data.filter(
    (item) => item.scanned === true && item.type === "Vegetable"
  );
  const snackData = data.filter(
    (item) => item.scanned === true && item.type === "Snack"
  );
  const dairyData = data.filter(
    (item) => item.scanned === true && item.type === "Dairy"
  );
  const proteinData = data.filter(
    (item) => item.scanned === true && item.type === "Protein"
  );
  const displayed = data.filter((item) => item.scanned == true);
  const data1 = {
    labels: ["Vegetable", "Dairy", "Protein", "Snack"],
    datasets: [
      {
        data: [
          vegetableData.length,

          dairyData.length,
          proteinData.length,
          snackData.length,
        ],
      },
    ],
    options: {
      responsive: true,
      maintainAspectRatio: false,
    },
  };

  const data2 = {
    labels: [
      "11/12",
      "11/13",
      "11/14",
      "11/15",
      "11/16",
      "11/17",
      "11/18",
      "11/19",
      "11/20",
    ],
    datasets: [
      {
        label: "Count",
        data: [12, 18, 15, 7, 17, 15, 12, 7, displayed.length],
        fill: true,
        backgroundColor: "rgba(75,192,192,0.2)",
        borderColor: "rgba(75,192,192,1)",
      },
    ],
    options: {
      responsive: true,
      maintainAspectRatio: false,
    },
  };

  return (
    <div className="outer">
      <div className="col">
        <h2>Consume in the next 3 days</h2>
        {loading ? (
          "Loading"
        ) : (
          <>
            {dataFiltered.map((item) => (
              <div
                className="fpItem"
                key={item._id}
                onClick={() => {
                  console.log(item.barcode);
                  setScanCode(item.barcode);
                  deleteEntry();
                }}
                style={{ backgroundColor: "rgba(255,0,0,0.5)" }}
              >
                <img src={item.photos[0]} alt="" className="fpImg" />
                <div className="info">
                  <span className="fpName">{item.name}</span>
                  <br />
                  Best Before
                  <br />
                  <span className="fpDate">
                    {new Date(item.expiry).getUTCMonth() +
                      1 +
                      "/" +
                      new Date(item.expiry).getUTCDate() +
                      "/" +
                      new Date(item.expiry).getUTCFullYear()}
                  </span>
                </div>
              </div>
            ))}
          </>
        )}
      </div>
      <div className="col">
        <h2>Consume in the next 14 days</h2>
        {loading ? (
          "Loading"
        ) : (
          <>
            {dataFiltered2.map((item) => (
              <div
                className="fpItem"
                key={item._id}
                style={{ backgroundColor: "rgba(100,255,100,0.5)" }}
                onClick={() => {
                  setScanCode(item.barcode);
                  deleteEntry();
                }}
              >
                <img src={item.photos[0]} alt="" className="fpImg" />
                <div className="info">
                  <span className="fpName">{item.name}</span>
                  <br />
                  Best Before
                  <br />
                  <span className="fpDate">
                    {new Date(item.expiry).getUTCMonth() +
                      1 +
                      "/" +
                      new Date(item.expiry).getUTCDate() +
                      "/" +
                      new Date(item.expiry).getUTCFullYear()}
                  </span>
                </div>
              </div>
            ))}
          </>
        )}
      </div>
      <div className="col" style={{ width: "300px" }}>
        <div className="consumption">
          <h2>Metrics</h2>
          <h3>Pantry Breakdown</h3>
          <Chart
            type="doughnut"
            data={data1}
            style={{ height: "200px !important", width: "200px !important" }}
          />
          <h3>Inventory Size</h3>
          <Chart type="line" data={data2} />
        </div>
      </div>
    </div>
  );
};
export default Body;
