import React, { useEffect } from "react";
import Quagga from "quagga";
import styled from "styled-components";

const ScanArea = styled.div`
  position: relative;
  width: 300px;
  height: 200px;
  transform: scale(0.7, 0.7);
  .viewport {
    position: absolute;
    top: -200px;
    left: -530px;
    overflow: hidden;
  }
  .scanBorder {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    border-radius: 20px;
    height: 100px;
    top: 60px;
    left: -5px;
    overflow: hidden;
    font-size: 40px;
    border: dashed 2px yellow;

    color: #fff;
    z-index: 666;
  }
`;

const Scanner = (props) => {
  const _onDetected = (result) => {
    let code = result;
    Quagga.stop();
    return props.handleScan(code);
  };

  const _onProcessed = (result) => {};

  useEffect(() => {
    Quagga.init(
      {
        inputStream: {
          type: "LiveStream",
          constraints: {
            width: { min: 800, max: 1280 },
            height: { min: 600, max: 720 },
            aspectRatio: { min: 4 / 3, max: 16 / 9 },
          },
          area: {
            top: "0%", // top offset
            right: "0%", // right offset
            left: "0%", // left offset
            bottom: "0%", // bottom offset
          },
        },
        frequency: "full",
        locator: {
          patchSize: "medium",
          halfSample: true,
        },
        numOfWorkers: 2,
        decoder: {
          readers: ["ean_reader"],
        },
        locate: true,
      },
      function (err) {
        if (err) {
          return console.log(err);
        }
        Quagga.start();
      }
    );
    Quagga.onDetected(_onDetected);
    Quagga.onProcessed(_onProcessed);
    return () => {
      Quagga.stop();
    };
  }, []);

  return (
    <ScanArea>
      <div id="interactive" className="viewport" />
      <div className="scanBorder">
        <div className="scan"></div>
      </div>
    </ScanArea>
  );
};

export { Scanner };
